    margin-top: 1%;
    margin-left: 5%;
    margin-right: 5%;
  }

  .table-name{
    font-size: 20px;
    padding: 1%;
  }

  .carrer-table{
    width: 90%;
    padding-left: 5%;
    padding-bottom: 2%;
  }

  .carrer-age-th{
    font-size: 16px;
    width: 30%;
    text-align: left;
    padding-bottom: 1%;
  }

  .carrer-sentence-th{
    font-size: 16px;
    text-align: left;
    padding-left: 10px;
  }

  .carrer-age-td{
    font-size: 14px;
    padding-left: 1%;
    padding-bottom: 1%;
  }

  .carrer-sentence-td{
    font-size: 14px;
    padding-left: 10px;
    padding-bottom: 1%;
  }

  .carrer-a{
    text-decoration: none;
  }
}