
@media screen and (min-width: 960px) {
  .header-root {
    padding: 10px 4% 10px;
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #fff;
    display: flex;
    align-items: center;
  }

  .header-title {
    margin: 0; 
    padding: 0;
    font-size: 32px;
  }

  .header-a {	
    text-decoration: none;
    color: #4b4b4b;
  }

  .header-contents {
    list-style: none;
    margin: 0;
    display: flex;
  }

  .header-content {
    margin: 0 0 0 25px;
    font-size: 18px;
  }

  .header-nav {
    margin: 0 0 0 auto;
  }
}


@media screen and (max-width: 959px) {
  .header-root {
    padding-top: 10px;
    padding-bottom: 10px;
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .header-title {
    margin: 0; 
    padding: 0;
    font-size: 32px;
  }

  .header-a {	
    text-decoration: none;
    color: #4b4b4b;
  }

  .header-contents {
    list-style: none;
    margin: 0;
    display: flex;
  }

  .header-content {
    margin: 0 20px 0 0;
    font-size: 18px;
  }

  .header-nav {
    justify-content: center;
  }
}
